<template>
  <div class="input-code">
    <div class="input-code__wrapper" v-for="item in amount" :key="item">
      <input
        v-model="code[item-1]"
        class="input-code__field"
        title="code"
        v-focus="(item - 1) === currentIndex"
        maxlength="1"
        placeholder="X"
        @input="handleInput($event, (item-1))"
        @keyup.delete="onDelete($event, (item-1))"
        @paste="handlePaste"
      >
    </div>
  </div>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref } from 'vue'

export default {
  name: 'InputCode',
  props: {
    amount: {
      type: Number,
      default: 6
    }
  },
  directives: {
    focus: {
      updated (el, obj) {
        obj.value && el.focus()
      }
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const code = ref(new Array(props.amount).fill(''))
    const currentIndex = ref(0)

    const handleInput = (e, index) => {
      currentIndex.value = index
      e.target.value = validateNumber(e.target.value)
      e.target.value !== '' && ++(currentIndex.value)
      !code.value.includes('') && emit('onCompleted', code.value.join(''))
    }

    const onDelete = (e, index) => {
      if (e.target.value === '') {
        currentIndex.value = index - 1
      }
    }

    const validateNumber = val => {
      return val.replace(/\D/g, '')
    }

    const handlePaste = (e) => {
      e.preventDefault()
    }
    return {
      t,
      code,
      currentIndex,
      validateNumber,
      handleInput,
      onDelete,
      handlePaste
    }
  }
}
</script>

