  width:100%;
  display: flex;
  justify-content: space-around;

  &__wrapper {
     width: 15%;
     height: 0;
     padding-bottom: 20%;
     position: relative;

    &:not(:last-of-type) {
      margin-right: 4px;
    }
   }

  &__field {
    width: 50px;
    height: 64px;
    left: 560px;
    top: 512px;

    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    border-radius: 4px;
    text-align: center;

    font-size: 34px;
